.gradient-reveal {
  // transition: opacity 0.5s;

  position: relative;

  &::after {
    position: absolute;
    content: "";
    top: 0;
    z-index: -5;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    background-image: $gradient-white;
    transition: all 1.5s;
    transform: translateY(100px);
  }

  &.y-gap {
    &::after {
      height: 65%;
      top: 50%;
    }

    &.finished {
      &::after {
        transform: translateY(-50%);
      }
    }
  }

  &.finished {
    &::after {
      opacity: 1;
      transform: translateY(0);
    }
  }

  &.bg-gradient-green {
    &.finished {
      &::after {
        opacity: 1;
        background-image: $gradient-green;
      }
    }
  }
  &.bg-gradient-pink {
    &.finished {
      &::after {
        background-image: $gradient-pink;
      }
    }
  }
}

.bg-gradient-green:not(.gradient-reveal) {
  background-image: $gradient-green;
}

.bg-gradient-pink:not(.gradient-reveal) {
  background-image: $gradient-pink;
}

.bg-pink-shade:not(.gradient-reveal) {
  background-color: $pink-shade;
}

