.header-top {
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 100;
  transition: all 0.3s;

  &.sticky {
    background-color: $white;
    box-shadow: 0 1px 4px 0 rgba($color: #000000, $alpha: 0.1);
  }
}

.navbar {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem 1rem;
  transition: all 0.3s;

  @include bp(mobile) {
    height: 58px;
  }

  @include bp(md) {
    padding: 1rem;
  }

  @include bp(lg) {
    padding: 1rem;
  }
}

.nav-logo {
  @include bp(mobile) {
    width: 126px;
  }

  &__img {
    height: auto;
    width: 80px;

    @include bp(md) {
      width: 110px;
    }

    @include bp(lg) {
      width: 120px;
    }
  }
}

.nav-toggler {
  position: relative;
  cursor: pointer;
  display: none;

  @include bp(mobile) {
    display: block;
  }

  .bar {
    width: 20px;
    height: 2px;
    display: block;
    background-color: $nav-toggler-color;
    transition: all 0.3s;
    backface-visibility: hidden;

    &:not(:last-child) {
      margin-bottom: 4px;
    }
  }
}

.transparent:not(.sticky) .nav-toggler:not(.active) .bar {
  background-color: #d4d4d4;
}

.nav-toggler.active {
  z-index: 102;

  .bar {
    &:nth-of-type(1) {
      width: 20px;
      transform: translate(0, 5px) rotate(45deg);
    }

    &:nth-of-type(2) {
      opacity: 0;
    }

    &:nth-of-type(3) {
      width: 20px;
      transform: translate(0, -7px) rotate(-45deg);
    }
  }
}

.header-top.sticky {
  .navbar {
    padding: 0.5rem 1rem;

    @include bp(md) {
      padding: 1rem;
    }
  }
}

.nav-menu-wrapper {
  @include bp(mobile) {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 101;
    background-color: $white;
    background-image: $gradient-pink;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s;
  }

  &.active {
    opacity: 1;
    visibility: visible;
  }
}

.nav-menu {
  @include bp(mobile) {
    margin-top: 80px;
  }

  &__link {
    @include fonts-px(16px, 16px, 16px, 15px, null);
    // text-shadow: 0 1px 2px rgba(0,0,0,0.4);
    letter-spacing: 1px;
    font-weight: 500;
    color: $nav-link;
    transition: all 0.2s ease-in;

    @include bp(mobile) {
      margin-bottom: 0.5rem;
      margin-left: 1rem;
    }

    &.active {
      color: $nav-link-active;
    }

    &:hover {
      text-decoration: none;
      color: #f1186f !important;
    }

    &:last-of-type {
      margin-right: 0;
    }

    @include bp(mobile) {
      display: block;
      padding: 0.5rem 1rem;
      font-weight: 600;
      text-shadow: none;
    }

    @include bp(md) {
      padding: 5px 0;
      margin: 0 1.5em;
    }

    @include bp(xl) {
      margin: 0 2.1em;
    }

    &:hover,
    &:focus {
      color: $nav-link-hover;
    }
  }
}

#life-header {
  height: 40px;
  width: 40px;
  margin-left: -3px;

  svg {
    stroke-width: 0.3px;
  }

  path {
    fill: #f13ab1;
    stroke: #f13ab1;
  }

  &.color1 {
    path {
      fill: #fd913c;
      stroke: #fd913c;
    }
  }

  &.color2 {
    path {
      fill: #f05524;
      stroke: #f05524;
    }
  }

  &.color3 {
    path {
      fill: #f13ab1;
      stroke: #f13ab1;
    }
  }
  
  @include bp('md'){
    height: 52px;
  }
}

.transparent {
  &.sticky {
    .nav-menu__link {
      color: $nav-link;
      text-shadow: none;

      &.active {
        color: $theme-primary;
      }
    }
  }

  .nav-menu__link {
    @include bp(md) {
      color: $white;
    }

    &.active {
      color: $theme-primary;
    }
  }
}

.nav-logo {
  display: flex;
  align-items: center;
}

