.life-stroke-1 {
  stroke-dasharray: 57 59;
  stroke-dashoffset: 58;
  animation: VfYDsRVP_draw_0 2500ms linear 0ms infinite,
    VfYDsRVP_fade 2500ms linear 0ms infinite;
}
.life-stroke-2 {
  stroke-dasharray: 34 36;
  stroke-dashoffset: 35;
  animation: VfYDsRVP_draw_1 2500ms linear 0ms infinite,
    VfYDsRVP_fade 2500ms linear 0ms infinite;
  animation: VfYDsRVP_draw_1 2500ms linear 0ms infinite,
    VfYDsRVP_fade 2500ms linear 0ms infinite;
}
.life-stroke-3 {
  stroke-dasharray: 88 90;
  stroke-dashoffset: 89;
  animation: VfYDsRVP_draw_2 2500ms linear 0ms infinite,
    VfYDsRVP_fade 2500ms linear 0ms infinite;
}
.life-stroke-4 {
  stroke-dasharray: 53 55;
  stroke-dashoffset: 54;
  animation: VfYDsRVP_draw_3 2500ms linear 0ms infinite,
    VfYDsRVP_fade 2500ms linear 0ms infinite;
}
@keyframes VfYDsRVP_draw {
  100% {
    stroke-dashoffset: 0;
  }
}
@keyframes VfYDsRVP_fade {
  0% {
    stroke-opacity: 1;
  }
  84% {
    stroke-opacity: 1;
  }
  100% {
    stroke-opacity: 0;
  }
}
@keyframes VfYDsRVP_draw_0 {
  4% {
    stroke-dashoffset: 100;
  }
  36% {
    stroke-dashoffset: 0;
  }
  100% {
    stroke-dashoffset: 0;
  }
}
@keyframes VfYDsRVP_draw_1 {
  6.666666666666668% {
    stroke-dashoffset: 35;
  }
  38.66666666666667% {
    stroke-dashoffset: 0;
  }
  100% {
    stroke-dashoffset: 0;
  }
}
@keyframes VfYDsRVP_draw_2 {
  9.333333333333334% {
    stroke-dashoffset: 89;
  }
  41.333333333333336% {
    stroke-dashoffset: 0;
  }
  100% {
    stroke-dashoffset: 0;
  }
}
@keyframes VfYDsRVP_draw_3 {
  12% {
    stroke-dashoffset: 54;
  }
  44% {
    stroke-dashoffset: 0;
  }
  100% {
    stroke-dashoffset: 0;
  }
}
