.section-title {
    color: $white;
    font-family: $mts;
    font-weight: 900;
    font-size: 27px;
    line-height: 42px;
    text-transform: uppercase;

    @include bp('md'){
        font-size: 45px;
        line-height: 37px;
    }

    @include bp('xl'){
        font-size: 60px;
        line-height: 52px;
    }

    &--small {
        font-weight: 700;
        font-size: 28px;
        line-height: 32px;
        
        @include bp('md'){
            font-size: 36px;
            line-height: 38px;
        }
        
        @include bp('lg'){
            font-size: 44px;
            line-height: 46px;
        }
    }
}

.section-sub-title {
    color: $white;
    font-family: $sspro;
    font-weight: 300;
    font-size: 18px;
    line-height: 20px;
    margin: 0;
    
    @include bp('md'){
        font-size: 22px;
        line-height: 22px;
    }

    &--footer {
        font-size: 16px;
        padding-top: 3px;
        max-width: 320px;
        width: 100%;
        line-height: 1.25;

        @include bp('xl'){
            padding-left: 17px;
        }
    }
}
