.button {
  // background-color: $white;
  background-color: transparent;
  border: none;
  cursor: pointer;
  // font-weight: 500;
  color: $theme-primary;
  min-width: 1rem;
  letter-spacing: 0.5px;
  font-size: 12px;
  border-radius: 4px;
  padding: 0.7rem 1.4rem;
  transition: all 0.3s;
  transition-timing-function: cubic-bezier(0.7, 0, 0.9, 1);
  display: inline-block;
  position: relative;
  overflow: hidden;
  z-index: 1;
  text-transform: uppercase;
  transform: skewX(-15deg);
  font-weight: 700;
  text-decoration: none !important;

  span {
    transform: skewX(15deg);
    display: block;
  }

  &::after {
    content: "";
    background-color: $theme-primary;
    z-index: -1;
    position: absolute;
    top: 50%;
    right: 100%;
    margin: -15px 0 0 1px;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    transform-origin: 100% 50%;
    transition: transform 0.6s, opacity 0.6s;
    transition-timing-function: cubic-bezier(0.7, 0, 0.9, 1);
    transform: scale3d(0, 2, 1);
  }

  &.reverse-slide {
    &::after {
      left: 100%;
      right: auto;
      transform: scale3d(1, 2, 1);
    }

    &:focus,
    &:hover {
      &::after {
        transform: scale3d(9, 9, 1);
      }
    }
  }

  @media (min-width: $md) {
    font-size: 14px;
    padding: 0.8rem 2rem;
  }

  &:focus,
  &:hover {
    outline: none;
    color: $white;

    &::after {
      // transform: scale3d(-50, 50, 1);
      transform: scale3d(-10,50,1);
    }
  }

  &-primary {
    background-color: $theme-primary;
    color: $white;

    &:focus,
    &:hover {
      color: $white;
    }
  }

  &-outline {
    border: 1px solid $theme-primary;
    background-color: transparent;

    &::after {
      content: "";
      background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg fill='%23ff3f6c' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 127 42'%3e%3cg%3e%3cg%3e%3cpath stroke-miterlimit='50' d='M1.31 2.243A1 1 0 0 1 2.28 1h122.567a1 1 0 0 1 .99 1.141l-4.72 33.04a1 1 0 0 1-.944.857L11.817 40.963a1 1 0 0 1-1.015-.757z'/%3e%3c/g%3e%3c/g%3e%3c/svg%3e");
      position: absolute;
    }

    &:focus,
    &:hover {
      border: 1px solid $theme-primary;
    }
  }

  &-outline-white {
    &,
    &:hover {
      border: 1px solid $white;
      color: $white;
      background-color: transparent;
    }

    &:after {
      background-color: $white;
      display: none;
    }
  }

  &-outline-fillwhite {
    &,
    &:hover {
      border: 1px solid $white;
      color: $white;
      background-color: transparent;
    }
    
    &:hover {
      background-color: $white;
      color: #f26d5b;
    }

    &:after {
      background-color: $white;
      display: none;
    }
  }

  &.rectangle {
    border-radius: 4px !important;
  }

  &.twitter {
    color: #7eade3 !important;
    border-color: #7eade3 !important;
  }

  &.linkedin {
    color: #4978ac !important;
    border-color: #4978ac !important;
  }

  &-white-transparent {
    border: 1px solid $white;
    color: $white;
    background-color: transparent;

    &:focus,
    &:hover {
      color: $white;
    }
  }

  &-slim {
    padding-top: 0.5rem !important;
    padding-bottom: 0.5rem !important;
  }

  &-linkedin {
    border-radius: 4px;
    background-color: #2876b4;
    color: $white;

    &:focus,
    &:hover {
      color: $white;
    }
  }
}

.button-md {
  padding: 14px 20px;
  border-radius: 40px;

  @include bp(md) {
    padding: 19px 40px;
    border-radius: 40px;
  }
}

.shape-btn {
  color: $theme-primary;
  padding: 7px 28px 12px 30px;
  letter-spacing: .5px;
  font-size: 16px;
  line-height: 1.46;
  display: inline-block;
  position: relative;
  transition: color 0.3s ease-in-out;
  min-width: 125px;
  text-align: center;
  font-weight: 600;
  appearance: none;

  span {
    position: relative;
    z-index: 1;
  }

  &::after {
    content: "";
    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg fill='%23ff3f6c' preserveAspectRatio='none' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 127 42'%3e%3cg%3e%3cg%3e%3cpath stroke-miterlimit='50' d='M1.31 2.243A1 1 0 0 1 2.28 1h122.567a1 1 0 0 1 .99 1.141l-4.72 33.04a1 1 0 0 1-.944.857L11.817 40.963a1 1 0 0 1-1.015-.757z'/%3e%3c/g%3e%3c/g%3e%3c/svg%3e");
    position: absolute;
    width: 100%;
    opacity: 0;
    height: 100%;
    transition: opacity 0.3s ease-in-out;
    top: 0;
    left: 0;
    background-size: 100% 100%;
    background-repeat: no-repeat;
  }

  &::before {
    content: "";
    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' preserveAspectRatio='none' viewBox='0 0 127 42'%3e%3cg%3e%3cg%3e%3cpath fill='none' stroke='%23ff3f6c' stroke-miterlimit='50' d='M1.31 2.243A1 1 0 0 1 2.28 1h122.567a1 1 0 0 1 .99 1.141l-4.72 33.04a1 1 0 0 1-.944.857L11.817 40.963a1 1 0 0 1-1.015-.757z'/%3e%3c/g%3e%3c/g%3e%3c/svg%3e");
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-size: 100% 100%;
    background-repeat: no-repeat;
  }

  &:hover {
    text-decoration: none;
    color: #ffffff;

    &::after {
      opacity: 1;
    }
  }

  &--white {
    &::after {
      background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg fill='%23ffffff' preserveAspectRatio='none' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 127 42'%3e%3cg%3e%3cg%3e%3cpath stroke-miterlimit='50' d='M1.31 2.243A1 1 0 0 1 2.28 1h122.567a1 1 0 0 1 .99 1.141l-4.72 33.04a1 1 0 0 1-.944.857L11.817 40.963a1 1 0 0 1-1.015-.757z'/%3e%3c/g%3e%3c/g%3e%3c/svg%3e");
    }

    &::before {
      background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' preserveAspectRatio='none' viewBox='0 0 127 42'%3e%3cg%3e%3cg%3e%3cpath fill='none' stroke='%23ffffff' stroke-miterlimit='50' d='M1.31 2.243A1 1 0 0 1 2.28 1h122.567a1 1 0 0 1 .99 1.141l-4.72 33.04a1 1 0 0 1-.944.857L11.817 40.963a1 1 0 0 1-1.015-.757z'/%3e%3c/g%3e%3c/g%3e%3c/svg%3e");
    }

    color: #ffffff;

    &:hover {
      color: #f26d5b;
    }
  }

  &--arrow {
    min-width: 0;
    padding: 5px 23px 13px;
    opacity: 0.5;
    transition: all 0.2s ease;

    &:hover {
      opacity: 1;
    }

    img {
      transform: scale(0.9);
    }

    &::after {
      display: none;
    }
  }
}

.button-lg {
  padding: 12px 30px;
  border-radius: 30px;

  @include bp(md) {
    padding: 16px 50px;
    border-radius: 50px;
  }
}

.twitter {
  &:hover,
  &:focus {
    color: $twitter !important;
  }
}

.facebook {
  &:hover,
  &:focus {
    color: $facebook !important;
  }
}

.linkedin {
  &:hover,
  &:focus {
    color: $linkedin !important;
  }
}
