@font-face {
  font-family: "Noe Display";
  src: url("../fonts/neo/NoeDisplay.woff2") format("woff2"),
    url("../fonts/neo/NoeDisplay.woff") format("woff"),
    url("../fonts/neo/NoeDisplay.svg#NoeDisplay") format("svg");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Noe Display";
  src: url("../fonts/neo/NoeDisplay-Black.woff2") format("woff2"),
    url("../fonts/neo/NoeDisplay-Black.woff") format("woff"),
    url("../fonts/neo/NoeDisplay-Black.svg#NoeDisplay-Black") format("svg");
  font-weight: 900;
  font-style: normal;
}

@font-face {
  font-family: "Noe Display";
  src: url("../fonts/neo/NoeDisplay-Medium.woff2") format("woff2"),
    url("../fonts/neo/NoeDisplay-Medium.woff") format("woff"),
    url("../fonts/neo/NoeDisplay-Medium.svg#NoeDisplay-Medium") format("svg");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: "Noe Display";
  src: url("../fonts/neo/NoeDisplay-Bold.woff2") format("woff2"),
    url("../fonts/neo/NoeDisplay-Bold.woff") format("woff"),
    url("../fonts/neo/NoeDisplay-Bold.svg#NoeDisplay-Bold") format("svg");
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: "Savoye LET Plain";
  src: url("../fonts/savoye/SavoyeLetPlain.eot");
  src: url("../fonts/savoye/SavoyeLetPlain.eot?#iefix")
      format("embedded-opentype"),
    url("../fonts/savoye/SavoyeLetPlain.woff2") format("woff2"),
    url("../fonts/savoye/SavoyeLetPlain.woff") format("woff"),
    url("../fonts/savoye/SavoyeLetPlain.svg#SavoyeLetPlain") format("svg");
  font-weight: 400;
  font-style: normal;
}

