input {
  outline: none;
  border: 0;
}

.input-group {
  position: relative;
  margin-bottom: 2rem;

  @include bp(lg) {
    margin-bottom: 3rem;
  }
}

.input-field,
.input-textarea {
  border: 0;
  border-bottom: 1px solid $border;
  padding: 0.5rem 0;
  font-weight: 500;
  letter-spacing: 0.5px;
  transition: all 0.3s;
  outline: none;
  min-width: 200px;

  @include fonts-px(14px, 16px, 18px, null, null);

  @include bp(lg) {
    min-width: 300px;
  }

  &:focus {
    border-color: $theme-primary;
  }

  &.round {
    padding: 1rem;
    border: 1px solid $border;
    border-radius: 40px;
  }
}

.input-field {
  // background-color: transparent;
  // border-bottom: 1px solid $input-colored-border;
  // color: $text-white-shade;
  // transition: all 0.3s;

  &::-webkit-input-placeholder {
    /* Chrome/Opera/Safari */
    color: rgba($color: $text-primary, $alpha: 0.8);
  }
  &::-moz-placeholder {
    /* Firefox 19+ */
    color: rgba($color: $text-primary, $alpha: 0.8);
  }
  &:-ms-input-placeholder {
    /* IE 10+ */
    color: rgba($color: $text-primary, $alpha: 0.8);
  }
  &:-moz-placeholder {
    /* Firefox 18- */
    color: rgba($color: $text-primary, $alpha: 0.8);
  }

  &:focus {
    // border-color: $text-white-shade;
  }
}

.input-field.with-button {
  padding-right: 160px !important;
}

.input-field.lg {
  padding: 12px 16px;

  @include bp(md) {
    padding: 18px 40px;
  }
}

.select {
  &--outline {
    background-color: transparent;
    border-radius: 20px;
    border: 1px solid $white;

    .current {
      color: $white;
    }
  }

  .list {
    width: 100%;
  }
}

/* File type */
.js .input-file {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}

.input-file-label {
  cursor: pointer;
  text-decoration: underline;
}

.input-file:focus + .input-file-label {
  outline: 1px dotted $theme-primary;
  outline: -webkit-focus-ring-color auto 5px;
}

.parsley-errors-list {
  margin: 0;
  padding: 0;
  color: red;
  font-size: 11px;

  li {
    list-style-type: none;
  }
}

.bg-colored,
.bg-blue .parsley-errors-list {
  color: $text-white-shade;
}

.checkbox-tag {
  opacity: 0;
  position: relative;
  left: 1rem;
  width: 1px;

  + label {
    box-shadow: 0 1px 10px 1px rgba(0, 0, 0, 0.08);
    border-radius: 20px;
    background-color: $white;
    padding: 6px 28px 6px 10px;
    position: relative;
    user-select: none;
    cursor: pointer;
    @include fonts-px(12px, 13px, 14px, null, null);
    margin-bottom: 1rem;

    @include bp(md) {
      margin-right: 1rem;
      padding: 10px 36px 10px 18px;
    }

    &::after {
      position: absolute;
      top: 48%;
      transform: translateY(-50%);
      right: 0.8rem;
      content: "+";

      @include bp(md) {
        right: 1rem;
      }
    }
  }

  &:checked + label {
    color: $theme-primary;
    font-weight: 700;

    &::after {
      content: "x";
    }
  }
}

.errorBlock {
  color: $theme-primary;
  position: relative;
  top: -2rem;
}
