.container {
    &--banner {
        @media (min-width: 1024px){
            max-width: 960px;
        }

        @media (min-width: 1200px) {
            max-width: 90%;
        }

        @media (min-width: 1366px) {
            max-width: 1320px;
        }
    }
}

.blk-353 {
    color: #353a50 !important;
}

.font-400 {
    font-weight: 400 !important;
}

.missing-content {
    padding-top: 120px;
    text-align: center;
    padding-bottom: 70px;
    min-height: calc(100vh - 280px);

    @media only screen and (max-width: 1199.9px) {
        padding-top: 100px;
        padding-bottom: 60px;
    }

    @media only screen and (max-width: 767.9px) {
        padding-bottom: 50px;
    }

    @media only screen and (max-width: 479.9px) {
        padding-bottom: 40px;
    }

    &__img {
        height: 254px;

        @media only screen and (max-width: 1199.9px) {
            height: 200px;
        }

        @media only screen and (max-width: 991.9px) {
            height: 180px;
        }

        @media only screen and (max-width: 767.9px) {
            height: 150px;
        }

        @media only screen and (max-width: 575.9px) {
            height: 120px;
        }

        @media only screen and (max-width: 479.9px) {
            height: 100px;
        }

        img {
            height: 100%;
        }
    }

    &__text {
        p {
            color: #4e5364;
            font-size: 27px;
            font-weight: 600;
            line-height: 1;
            margin-top: 10px;

            @media only screen and (max-width: 1199.9px) {
                font-size: 24px;
                margin-top: 20px;
            }
    
            @media only screen and (max-width: 767.9px) {
                font-size: 20px;
            }
    
            @media only screen and (max-width: 479.9px) {
                font-size: 16px;
                margin-top: 30px;
            }
        }

        a {
            margin-top: 50px;

            &::after {
                z-index: -1;
            }

            @media only screen and (max-width: 1199.9px) {
                margin-top: 40px;
            }
    
            @media only screen and (max-width: 767.9px) {
                margin-top: 30px;
            }

            @media only screen and (max-width: 479.9px) {
                margin-top: 20px;
                font-size: 14px;
                padding: 7px 18px 12px 20px;
            }
        }
    }
}